.app {
  text-align: center;
  color: white;
  min-height: 100vh;
  background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.app-header {
  display: flex;
  justify-content: flex-end;
  font-size: small;
  padding: 1rem;
}

.app-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
}

.info-link {
  font-size: small;
  margin: 1rem;
}

h3 {
  padding: 1rem;
}

button {
  margin: 1rem;
}

input {
  max-width: 20rem;
  margin: 1rem;
}

label {
  font-size: medium;
  margin: 1rem;
  min-width: 5rem;
  max-width: 5rem;
}

.form-group {
  display: flex;
  margin: 0%;
  justify-content: center;
  width: 100vw;
}

.error {
  font-size: small;
  color: red;
  max-width: 26rem;
}

.link {
  margin: 0;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: small;
  border: none;
  background: none;
  box-shadow: none;
  padding: 0;
  color: lightblue;
}
.link:hover {
  border: none;
  background: none;
  box-shadow: none;
}
.link:active {
  border: none !important;
  background: none !important;
  box-shadow: none !important;
}

.dropdown-toggle {
  border: none !important;
  background: none !important;
  box-shadow: none !important;
}